<template>
  <v-row justify="center">
    <v-dialog v-model="dialog" persistent max-width="600px">
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          color="primary"
          elevation="2"
          class="text-h6"
          outlined
          dark
          v-bind="attrs"
          v-on="on"
        >Hire Me!</v-btn>
      </template>
      <v-card>
        <v-card-title>
          <span class="headline">Contact Me</span>
        </v-card-title>
        <v-form ref="form" v-model="valid" @submit.prevent="sendHireMeMail">
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-text-field
                  label="Name"
                  type="text"
                  required
                  :rules="[(v) => !!v || 'Name is required']"
                  v-model="name"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label="E-mail"
                  type="email"
                  required
                  :rules="emailRules"
                  v-model="email"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  label="Message"
                  rows="3"
                  required
                  :rules="[(v) => !!v || 'Message is required']"
                  v-model="message"
                ></v-textarea>
              </v-col>
            </v-row>
            <v-spacer></v-spacer>
            <v-btn text plain @click="dialog = false">Close</v-btn>
            <v-btn color="success" type="submit" @click="dialog = false" :disabled="!valid">Send</v-btn>
          </v-container>
        </v-form>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
export default {
  name: "HireMe",
  data() {
    return {
      dialog: false,
      valid: true,
      name: "",
      email: "",
      message: "",
      emailRules: [
        v => !!v || "E-mail is required",
        v => /.+@.+/.test(v) || "E-mail must be valid",
        v => (v || "").indexOf(" ") < 0 || "No spaces are allowed"
      ]
    };
  },

  methods: {
    async sendHireMeMail() {
      try {
        const res = await this.$firebaseDatabase.collection("messages").add({
          to: "luistrcastro@gmail.com",
          message: {
            subject: "Hire Me Contact",
            text: 'You received an email from your "Hire Me" form.',
            html: `<p>You received an email from your "Hire Me" form. </p> \
          <p>Name: ${this.name}</p> \
          <p>E-mail: ${this.email}</p> \
          <p>Message: ${this.message}</p>`,
            time: new Date()
          }
        });
        console.log(res);
        this.$refs.form.reset();
      } catch (error) {
        console.log(error);
      }
    }
  }
};
</script>

<style></style>
