import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '@/views/Home.vue';
import ViewExercises from '@/views/ViewExercises.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/exercises',
    name: 'Exercises',
    component: ViewExercises,
    children: [
      {
        path: 'calculator',
        name: 'Calculator',
        component: () => import('../components/calculator/CalcHome.vue'),
      },
      {
        path: 'petshop',
        name: 'Petshop',
        component: () => import('../views/petshop/Petshop.vue'),
        children: [
          {
            path: 'pet',
            name: 'Pet',
            component: () => import('../views/petshop/Pet.vue'),
          },
          {
            path: 'ecommerce',
            name: 'Ecommerce',
            component: () => import('../views/petshop/Ecommerce.vue'),
            children: [
              {
                path: 'store',
                name: 'ProductsList',
                component: () =>
                  import('../views/petshop/products/ProductsList.vue'),
              },
              {
                path: ':id',
                name: 'ProductDetail',
                component: () =>
                  import('../views/petshop/products/ProductDetail.vue'),
              },
            ],
          },
        ],
      },
    ],
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
